import classNames from "classnames";
import React, { useEffect, useState } from "react";
import { FiGithub, FiLinkedin, FiMessageSquare } from "react-icons/fi";
import styles from "./App.module.css";
import ButtonLink from "./components/ButtonLink/ButtonLink";
import Project from "./components/Project/Project";
import SocialLinks from "./components/SocialLinks/SocialLinks";
import projectData from "./projectData";

function checkIsNarrow() {
  return window.innerWidth <= 900;
}

const App: React.FC = () => {
  const [isNarrow, setIsNarrow] = useState(checkIsNarrow());

  useEffect(() => {
    function handleResize() {
      setIsNarrow(checkIsNarrow());
    }

    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const sketchSvg = require("./images/svgs/sketch.svg");
  const sketchMobileSvg = require("./images/svgs/sketchMobile.svg");
  const logoSvg = require("./images/svgs/logo.svg");
  const projectsBackgroundSvg = require("./images/svgs/projectsBackground.svg");

  const projects = projectData.map((project) => (
    <li className={styles.projectContainer}>
      <Project
        title={project.title}
        description={project.description}
        codeUrl={project.codeUrl}
        demoUrl={project.demoUrl}
        img={project.img}
        labels={project.labels}
      />
    </li>
  ));

  return (
    <main>
      <div className={styles.app}>
        <header>
          <div className={styles.leftHeader}>
            <span className={styles.logoText}>
              <img src={logoSvg} alt="Logo" className={styles.logo}></img>
              David Schach
            </span>
            <nav>
              <a href="#projects">Projects</a>
              {/* <a href="/">Skills</a> */}
              <a href="#contact">Contact me</a>
            </nav>
          </div>
          {isNarrow ? null : <SocialLinks></SocialLinks>}
        </header>
        <div className={styles.landing}>
          <div className={styles.titleTextContainer}>
            <h1 className={styles.title}>Passionate Software Developer</h1>
            <h2 className={styles.subTitle}>
              currently doing his masters in computer science in media at HTW
              Berlin.
            </h2>
            <div className={styles.description}></div>
            <div className={styles.buttonWrapper}>
              <ButtonLink href="mailto:schachdavid@web.de">
                Get In Touch
              </ButtonLink>
            </div>
            {isNarrow ? <SocialLinks></SocialLinks> : null}
          </div>
          <div className={styles.artworkContainer}>
            <img
              src={isNarrow ? sketchMobileSvg : sketchSvg}
              alt="Developer Self Portrait Sketch"
              className={styles.sketch}
            ></img>
          </div>
        </div>
        <div className={styles.projectsBackgroundContainer}>
          <img
            src={projectsBackgroundSvg}
            alt="background artwork"
            className={styles.projectsBackground}
          ></img>
        </div>
        <a id="projects"></a>
        <h2 className={classNames(styles.projectsHeadline, styles.title)}>
          Projects
        </h2>
        <ul className={styles.projectsContainer} data-aos="fade-left">
          {projects}
        </ul>
        <a id="contact"></a>
        <footer className={styles.footer} data-aos="fade-up">
          Got Interested? Get in Touch with Me:
          <div className={styles.socialMediaFooter}>
            <a
              href="mailto:schachdavid@web.de"
              className={styles.socialMediaLink}
              aria-label="Send an Email"
            >
              <FiMessageSquare className={styles.icon} />
            </a>
            <a
              href="https://www.linkedin.com/in/david-schach-668a22137/"
              className={styles.socialMediaLink}
              aria-label="View LinkedIn Profile"
            >
              <FiLinkedin className={styles.icon} />
            </a>
            <a
              href="https://github.com/schachdavid"
              className={styles.socialMediaLink}
              aria-label="View Github Profile"
            >
              <FiGithub className={styles.icon} />
            </a>
          </div>
        </footer>
      </div>
    </main>
  );
};

export default App;
